.PaddingBottom {
  padding-bottom: 51px;
}

.SectionTitle {
  margin-left: 18px;
}

.Members {
  background-color: var(--theme-color-0);
  border-radius: 8px;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 4px;
  margin: 8px;
  padding: 8px 0;
}

.Button,
.MakeGameButton {
  background-color: var(--theme-color-4);
  bottom: 0;
  border: none;
  color: var(--theme-color-0);
  padding: 18px;
  width: 100%;

  transition: background-color 120ms;
}

.MakeGameButton {
  position: fixed !important;
  z-index: 1;
}

.MakeGameButton:disabled {
  background-color: lightgray !important;
}

.ButtonGroup {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 10px;
}

.MembersTab {
  display: flex;
  justify-content: space-between;
}

.EditMembersButton {
  padding-right: 10px;
}
